<template>
  <div class="context">
    <div class="content">
      <h3 class="title">Редактировать аккаунт TalkyTime ({{ $route.params.id }}):</h3>
      <div class="input-group mb-3">
        <span class="input-group-text" id="loginDescription">Логин</span>
        <input v-model="email" type="text" class="form-control" id="login-input"
               aria-describedby="loginDescription"
               placeholder="Введите логин">
      </div>
      <div class="input-group mb-3">
        <span class="input-group-text" id="passwordDescription">Пароль</span>
        <input v-model="password" type="text" class="form-control" id="password-input"
               aria-describedby="passwordDescription"
               placeholder="Введите пароль">
      </div>
      <div class="custom-control custom-switch">
        <input v-model="enabled" type="checkbox" class="custom-control-input" id="Enabled">
        <label class="custom-control-label" for="Enabled">Активный</label>
      </div>
      <div class="btn-group" role="group">
        <button v-on:click="editTalkyTime" class="btn btn-success btn-lg">Обновить</button>
        <button v-on:click="deleteTalkyTime" class="btn btn-danger btn-lg">Удалить</button>
      </div>
    </div>
  </div>
</template>

<script>
import "sweetalert2/dist/sweetalert2.css";
import Connection from "@/rsocket/connection";
import notification from "@/notify/notification";
import Swal from "sweetalert2";
import router from "@/views/router/routes";
import Vue from "vue";

export default {
  name: "EditTalkyTime",
  mounted() {
    this.id = this.$route.params.id;
    Connection.talkyTimeData(this.id, (res) => {
      let account = JSON.parse(res.data.toString());
      this.email = account.email;
      this.password = account.password;
      this.enabled = account.enabled;
    }, (error) => {
      notification("error", error.prettyText)
      router.push("/dashboard/talkytime/accounts");
    });
  },
  methods: {
    editTalkyTime() {
      if (this.email.trim().length === 0) {
        notification('warning', 'Введите логин');
        return;
      }
      if (this.password.trim().length === 0) {
        notification('warning', 'Введите пароль');
        return;
      }
      Swal.fire({
        title: 'Вы уверены?',
        text: "Данное действие внесет изменения в аккаунт TalkyTime!",
        icon: 'question',
        showCancelButton: true,
        confirmButtonText: 'Да! Я знаю, что делаю.',
        cancelButtonText: 'Нет! Я еще подумаю.'
      }).then((result) => {
        if (result.isConfirmed) {
          Connection.updateTalkyTime(this.id, this.email.trim(), this.password.trim(), this.enabled, (res) => {
            notification("success", "Аккаунт TalkyTime успешно обновлен");
            router.push("/dashboard/talkytime/accounts");
          }, (error) => {
            notification('error', error.prettyText);
          });
        }
      });
    },
    deleteTalkyTime() {
      Swal.fire({
        title: 'Вы уверены?',
        text: "Данное действие удалить аккаунт TalkyTime!",
        icon: 'question',
        showCancelButton: true,
        confirmButtonText: 'Да! Я знаю, что делаю.',
        cancelButtonText: 'Нет! Я еще подумаю.'
      }).then((result) => {
        if (result.isConfirmed) {
          Connection.deleteTalkyTime(this.id, (res) => {
            notification("success", "Аккаунт TalkyTime успешно удален");
            router.push("/dashboard/talkytime/accounts");
          }, (error) => {
            notification('error', error.prettyText);
          });
        }
      });
    }
  },
  data() {
    return {
      id: -1,
      email: "username",
      password: "password",
      enabled: false
    }
  }
}
</script>

<style lang="scss" scoped>
.content {
  box-sizing: border-box;
  background: #222427;
  padding: 10px;
  margin: 10px;
  border-radius: 10px;
  color: white;
  text-align: center;
}
</style>